.header-container {
  width: 100%;
  height: 88x;
  display: flex;
  z-index: 1;
  position: fixed;
  align-items: flex-start;
  padding-top: 22px;
  padding-left: 22px;
  padding-right: 22px;
  flex-direction: column;
  padding-bottom: 22px;
  justify-content: center;
  background-color: #181818;
}
.header-container1 {
  flex: 0 0 auto;
  width: 100%;
  height: 44px;
  display: flex;
  position: relative;
  max-width: 1101px;
  align-self: center;
  align-items: center;
  justify-content: flex-start;
}
.header-image {
  width: 100px;
  display: none;
  object-fit: cover;
}
.header-container2 {
  flex: 0 0 auto;
  width: auto;
  display: flex;
  align-items: flex-start;
}
.header-navlink {
  display: contents;
}
.header-image1 {
  width: 130x;
  object-fit: cover;
  text-decoration: none;
}
.header-container3 {
  flex: 1;
  width: auto;
  height: 100%;
  display: flex;
  align-self: center;
  align-items: center;
  justify-content: flex-end;
}
.header-navlink1 {
  transition: 0.3s;
  text-decoration: none;
}

.header-navlink2 {
  color: rgba(255, 255, 255, 0.5);
  transition: 0.3s;
  text-decoration: none;
}

.header-link {
  color: rgba(255, 255, 255, 0.5);
  transition: 0.3s;
}

.header-link1 {
  color: rgba(255, 255, 255, 0.5);
  transition: 0.3s;
}








@media(max-width: 767px) {
  .header-image {
    width: 30px;
    display: flex;
  }
  .header-container2 {
    flex: 1;
    justify-content: center;
  }
  .header-container3 {
    display: none;
  }
}
